body,
h1,
h2,
h3,
h4,
p,
ul,
ol {
  margin: 0px;
  padding: 0px;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

// colors
$accentColor: #d41443;
$subAccentColor: #ffa5ba;
$backgroundColor: #ffb8ca;
$productsBgColor: #e5e5e5;
$footerBgColor: #fff5f6;
$articleColor: #907e82;
$whiteColor: #ffffff;
$blackColor: #000000;

// fonts
$mainFont: 'DM Sans', sans-serif;
$titleFont: 'Titan One', sans-serif;
